<template>  
  <textarea :id="id" :value="value"></textarea>  
</template>
<script>
// Import TinyMCE
import dao from '../common/dao.js'
// https://www.tinymce.com/docs/demo/full-featured/
// https://blog.csdn.net/adsadadaddadasda/article/details/80340026
import tinymce from "tinymce/tinymce";  // 文本编辑 
import "tinymce/themes/modern/theme";
import "tinymce/plugins/advlist";
import "tinymce/plugins/link";
import "tinymce/plugins/image";
import "tinymce/plugins/code";
import "tinymce/plugins/table";
import "tinymce/plugins/textcolor";
import "tinymce/plugins/paste";
import "tinymce/plugins/textcolor";
import "tinymce/plugins/colorpicker";

const INIT = 0;
const CHANGED = 2;
var EDITOR = null;
export default {
  props: {
    value: {
      type: String,
      required: true
    },
    setting: {},
    url: {
      // 接口
      default: "",
      type: String
    },
    accept: {
      // 文件类型
      default: "image/jpeg, image/png",
      type: String
    },
    maxSize: {
      // 大小
      default: 2097152,
      type: Number
    },
    withCredentials: {
      default: false,
      type: Boolean
    }
  },
  watch: {
    value: function(val) {
      // console.log("init " + val);
      if (this.status == INIT || tinymce.activeEditor.getContent() != val) {
        tinymce.activeEditor.setContent(val);
      }
      this.status = CHANGED;
    }
  },
  data: function() {
    return {
      status: INIT,
      id: "editor-" + new Date().getMilliseconds()
    };
  },
  methods: {
    initEdittor () {
      const _this = this;
      const setting = {
        selector: "#" + _this.id,
        language: "zh_CN",
        language_url: "/tinymce/zh_CN.js",
        skin_url: "/tinymce/skins/lightgray",
        init_instance_callback: function(editor) {
          EDITOR = editor;
          // console.log("Editor: " + editor.id + " is now initialized.");
          editor.on("input change undo redo", () => {
            var content = editor.getContent();
            _this.$emit("input", content);
          });
        },
        content_style: `  
          * { padding:0; margin:0; }  
          html, body { height:100%; }  
          img { max-width:100%; display:block;height:auto; }  
          a { text-decoration: none; }  
          iframe { width: 100%; }  
          p { line-height:1.6; margin: 0px; }  
          table { word-wrap:break-word; word-break:break-all; max-width:100%; border:none; border-color:#999; }  
          .mce-object-iframe { width:100%; box-sizing:border-box; margin:0; padding:0; }  
          ul,ol { list-style-position:inside; }
        `,
        insert_button_items: "image link | inserttable",
        paste_retain_style_properties: "all",
        paste_word_valid_elements: "*[*]", // word需要它
        paste_data_images: true, // 粘贴的同时能把内容里的图片自动上传，非常强力的功能
        paste_convert_word_fake_lists: false, // 插入word文档需要该属性
        paste_webkit_styles: "all",
        paste_merge_formats: true,
        nonbreaking_force_tab: false,
        paste_auto_cleanup_on_paste: false,
        plugins: [
          "advlist link image",
          "code",
          "table textcolor paste textcolor colorpicker"
        ],
        toolbar_items_size: "small",
        block_formats:
          "Paragraph=p;Heading 1=h1;Heading 2=h2;Heading 3=h3;Heading 4=h4;Heading 5=h5;Heading 6=h6;",
        toolbar1:
          "table |insertfile undo redo | formatselect | link unlink | image", // 工具栏1
        toolbar2:
          " fontsizeselect | forecolor backcolor | fontselect bold italic underline strikethrough | alignleft aligncenter alignright alignjustify | outdent indent | removeformat | code", // 工具栏2
        // 图片上传
        images_upload_handler: function(blobInfo, success, failure) {
          if (blobInfo.blob().size > _this.maxSize) return failure("文件体积过大")
          if (_this.accept.indexOf(blobInfo.blob().type) >= 0) {
            _this.uploadPic(blobInfo, success, failure)
          } else {
            failure("图片格式错误");
          }
        }
        // init_instance_callback: function (ed) {
        //   ed.execCommand('mceImage');
        // }
      };
      Object.assign(setting, _this.setting);
      tinymce.init(setting);
    },
    async uploadPic (blobInfo, success, failure) {
      let formdata = new FormData()
      formdata.set('upload_img', blobInfo.blob())
      let { data } = await dao.articleImg(formdata)
      if (data.code === '200') {
        success(`${window.nodeHref}${data.href}`);
        return this.$emit("on-upload-success", data)
      }else return this.$emit("on-upload-fail", data);
    }
  },
  mounted() {
    this.initEdittor()
  },
  beforeDestroy: function() {
    tinymce.get(this.id).destroy();
  }
};
</script>  
