<template>
  <div class="articleList">
    <h4 class="marginB20">文章列表:</h4>
      <el-table
      :data="tableData"
      border
      stripe
      :header-cell-style="headerCellStyle"
      :cell-style="cellStyle"
      style="width: 100%"
      >
      <el-table-column
        fixed
        prop="title"
        label="标题"
        width="200"
        show-overflow-tooltip
        >
      </el-table-column>
      <el-table-column
        prop="article_id"
        label="文章ID"
        width="80">
      </el-table-column>
      <el-table-column
        prop="author"
        label="作者"
        width="80">
      </el-table-column>
      <el-table-column
        prop="category_id"
        label="所属分类"
        min-width="400"
        >
        <template slot-scope="scope">
            <el-tag 
              v-for="(item, index) in scope.row.category_id"
              :key="index">{{ catItem(item) }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="label_id"
        label="所属标签"
        min-width="400"
        >
        <template slot-scope="scope">
            <el-tag 
              v-for="(item, index) in scope.row.label_id"
              :key="index">{{ labItem(item) }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="commentsNumber"
        label="评论条数"
        width="80"
        >
      </el-table-column>
      <el-table-column
        prop="introduce_text"
        label="介绍文本"
        width="400"
        show-overflow-tooltip
        >
      </el-table-column>
      <el-table-column
        prop="release_date"
        label="发布日期"
        width="100"
        >
      </el-table-column>
      <el-table-column
        prop="create_date"
        label="创建日期"
        width="100"
        >
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="160">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import util from '../common/util.js'
import dao from '../common/dao.js'
  export default {
    name: 'articleList',
    props: {
    },
    data () {
      return {
        headerCellStyle: {
          'text-align': 'center',
          'background-color': '#f3f3f3'
        },
        cellStyle: {
          'text-align': 'center',
        },
        tableData: []
      }
    },
    watch: {
    },
    computed: {
      ...mapGetters(["getUser", "getCatList", "getLabList"])
    },
    methods: {
      ...mapActions(["setUser", "setCatList", "setLabList", "setArtId"]),
      async initArticle () {
        let {data: { articleList } } = await dao.articleAll()
        this.tableData = articleList
      },
      catItem (id) {
        return util.catItem(id, this.getCatList)
      },
      labItem (id) {
        return util.labItem(id, this.getLabList)
      },
      // 编辑
      handleEdit(index, row) {
        this.$router.push(`/home/articleWrite/${row.article_id}`)
      },
      // 删除
      handleDelete(index, row) {
        this.$confirm(`确定删除 ${row.title} ？`).then( async ()=>{
          let {data} = await dao.getArticleDelete({id: row.article_id})
          util.messageMethod('success', data.msg)
          this.initArticle()
        }).catch(() => {
          util.messageMethod("info", '已取消删除')
        });
      }
    },
    mounted () {
      this.initArticle()
    }
  }
</script>

<style lang="scss">
.articleList {
  h4 {
    line-height: 40px
  }
  .el-tag {
    margin-right: 5px
  }
}
</style>
